/* You can add global styles to this file, and also import other style files */


@font-face {
    font-family: "Helvetica";
    src: local("./assets/fonts/Helvetica.ttf");
}

:root {
    --gkn-primary-color: #20488e;
    --gkn-primary-color-variant-one: rgba(32, 72, 142, 1);
    --gkn-primary-color-variant-two: rgba(49, 93, 169, 1);
    --gkn-primary-color-variant-three: #2e5aa5;
    --gkn-primary-color-variant-four: rgb(0, 123, 255);
    --gkn-primary-color-variant-five: #315da9;
    --gkn-secondary-color: #f7ce1b;
    --gkn-tertiary-color: #dd0a04;
    --gkn-quaternary-color: green;
    --gkn-quintonary-color: #e5e9ea;
    --gkn-white-color: #ffffff;
    --gkn-black-color: #000000;
    --gkn-shadow-color: rgba(133, 133, 133, 1);
    --gkn-disable-color: #bdbdbd;
}

/*----- Elements----- */

/* .active{
	color: var(--gkn-primary-color) !important;
} */


*:not(mat-icon) {
  font-family: "Rubik", sans-serif !important;
}


.mat-nav-list a.active {
	background: rgba(25, 58, 117, 1);
	color: white;
}

html {
    height: 100%;
    overflow-x: hidden;
}

body {
    margin: 0 auto;
    font-family: "Rubik", sans-serif !important;
    background-color: #e5e9ea;
    min-width: 400px;
    /*overflow-y: hidden;*/
    overflow-x: hidden;
}

td,
p {
    font-family: "Rubik", sans-serif !important;
}

th {
    font-weight: bold;
}

h1,
h2,
h3,
h4,
h5,
h6,
th {
    font-family: "Helvetica", sans-serif !important;
}

button {
    font-family: "Helvetica", sans-serif !important;
}

span {
    font-family: "Rubik", sans-serif !important;
}


/* Elements */


/* Config style Angular Material */

simple-snack-bar>span {
    color: var(--gkn-white-color) !important;
}

mat-horizontal-stepper {
    border-radius: 0px;
}

mat-icon {
    color: var(--gkn-primary-color);
}

.mat-progress-spinner circle,
.mat-spinner circle {
    stroke: var(--gkn-primary-color) !important;
}

.ng-star-inserted {
    stroke: var(--gkn-primary-color) !important;
}

circle {
    stroke: var(--gkn-primary-color) !important;
}


