/* --- STYLES FOR APP --- */

.background-gkn-blue {
    background: rgb(32, 72, 142);
    background: linear-gradient( 0deg, rgba(32, 72, 142, 1) 0%, rgba(49, 93, 169, 1) 100%);
    -webkit-box-shadow: 0px 0px 16px 5px rgba(133, 133, 133, 1);
    -moz-box-shadow: 0px 0px 16px 5px rgba(133, 133, 133, 1);
    box-shadow: 0px 0px 16px 5px rgba(133, 133, 133, 1);
}

.background-gkn-white {
    -webkit-box-shadow: 0px 5px 16px 5px rgba(133, 133, 133, 1);
    -moz-box-shadow: 0px 5px 16px 5px rgba(133, 133, 133, 1);
    box-shadow: 0px 5px 16px 5px rgba(133, 133, 133, 1);
}

.background-gkn-blue-simple {
    background-color: var(--gkn-primary-color-variant-five) !important;
}

.background-gkn-blue-white-simple {
    background-color: var(--gkn-quintonary-color) !important;
}

.center-div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.btn-gkn-regular {
    width: 100%;
    height: 45px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 12.5px !important;
    text-align: justify;
    background-color: var(--gkn-secondary-color) !important;
    color: var(--gkn-primary-color) !important;
    font-size: 1.1em;
}

.btn-gkn-regular-disabled {
    width: 100%;
    height: 45px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 12.5px !important;
    text-align: justify;
    font-size: 1.1em;
    cursor: not-allowed !important;
}

.btn-gkn-simple {
    width: 100%;
    height: 45px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 12.5px !important;
    color: var(--gkn-primary-color) !important;
    font-size: 1.1em;
}

.container-stepper-h {
    background-color: var(--gkn-white-color) !important;
