}

.btn-gkn-min-light {
    color: #20488e;
    box-shadow: 0px 0px 16px 5px rgba(133, 133, 133, 1);
    border-radius: 50px;
    text-align: justify;
    font-weight: 600;
}

.btn-gkn-min-blue-modal {
    background-color: #20488e !important;
    color: var(--gkn-white-color) !important;
