}

.btn-gkn-primary {
    background-color: var(--gkn-primary-color) !important;
    color: var(--gkn-white-color) !important;
}


/* BUTTONS */

.btn-gkn-secondary {
    background-color: var(--gkn-secondary-color) !important;
    /*#f7ce1b !important;*/
    color: var(--gkn-primary-color);
    box-shadow: 0px 0px 16px 5px var(--gkn-shadow-color);
}

.btn-gkn-quintonary {
    background-color: var(--gkn-white-color) !important;
    /*#f7ce1b !important;*/
    color: var(--gkn-primary-color);
    box-shadow: 0px 0px 16px 5px var(--gkn-shadow-color);
}


/* Buttons blancos. Usar con mat-stroked-button*/

.btn-gkn-tertiary {
    color: var(--gkn-primary-color);
    box-shadow: 0px 0px 16px 5px var(--gkn-shadow-color);
}

.btn-gkn-quaternary {
    background-color: var(--gkn-quaternary-color);
    box-shadow: 0px 0px 16px 5px var(--gkn-shadow-color);
    border-radius: 5em;
    color: var(--gkn-white-color) !important;
}

.btn-gkn-link {
    color: var(--gkn-primary-color-variant-four) !important;
    font-weight: 600;
    cursor: pointer;
}

.btn-gkn-primary-icon {
    color: var(--gkn-primary-color) !important;
    cursor: pointer;
    font-size: 1.5em;
    font-weight: lighter;
}

.btn-gkn-secondary-icon {
    color: var(--gkn-secondary-color) !important;
    cursor: pointer;
    font-size: 1.5em;
    font-weight: lighter;
}

.btn-gkn-quaternary-icon {
    color: var(--gkn-quaternary-color) !important;
    cursor: pointer;
    font-size: 1.5em;
    font-weight: bold;
}

