    color: var(--gkn-tertiary-color) !important;
    cursor: pointer;
    font-size: 1.5em;
    font-weight: bold;
}

.disable-icon {
	cursor: not-allowed !important;
	color: gray;
}
.active-icon {
	cursor: pointer !important;
	/* color: inherit; */
}

.disable {
	cursor: not-allowed !important;
}
.disable-btn {
	cursor: not-allowed !important;
}

button:disabled {
    cursor: not-allowed !important;
    pointer-events: all !important;
}

.mat-button-toggle-disabled .mat-button-toggle-button {
    cursor: not-allowed !important;
    pointer-events: all !important;
}

.mat-standard-chip {
	height: auto !important;
}


