		border-radius: 12.5px !important;
		font-size: 1.1em;
		text-align: justify;
		cursor: not-allowed !important;
	}
	.btn-gkn-min-disabled-modal {
		background-color: #bdbdbd !important;
		box-shadow: 0px 0px 16px 5px rgba(133, 133, 133, 1);
		border-radius: 50px;
		text-align: justify;
		font-weight: 600;
		cursor: not-allowed !important;
	}
	.gkn-disabled {
		cursor: not-allowed !important;
		background-color: var(--gkn-disable-color) !important;
		color: var(--gkn-primary-color) !important;
	}
	.gkn-disabled-variant {
		cursor: not-allowed !important;
		background-color: var(--gkn-disable-color) !important;
	}
	.container-stepper-h {
		background-color: var(--gkn-white-color) !important;
		border-radius: 10px;
	}
	.mat-horizontal-content-container {
		padding: 0 0 24px 0 !important;
	}
	.mat-form-field-appearance-fill .mat-form-field-flex {
		background-color: var(--gkn-white-color) !important;
		border-radius: 12.5px !important;
	}
	.background-container {
		background-color: var(--gkn-primary-color-variant-three);
		padding: 15px;
