/*Loading*/

.loading-indicator {
    position: absolute;
    z-index: 999;
    width: 97.5%;
    overflow: show;
    margin: auto;
    height: 79.5vh;
}

.loading-indicator:before {
    content: "";
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
}

